/* ## Column Classes (including fifths)
--------------------------------------------- */
@media (min-width: 48em) {
  .one-half,
  .one-third,
  .one-fourth,
  .one-fifth,
  .one-sixth,
  .two-thirds,
  .two-fourths,
  .two-fifths,
  .two-sixths,
  .three-fourths,
  .three-fifths,
  .three-sixths,
  .four-fifths,
  .four-sixths,
  .five-sixths {
    float: left;
    margin-left: 15px;
  }
  .one-half,
  .three-sixths,
  .two-fourths {
    width: calc((100% - 15px) / 2);
  }
  .one-third,
  .two-sixths,
  .gallery-columns-3 .gallery-item {
    width: calc((100% - 2 * 15px) / 3);
  }
  .four-sixths,
  .two-thirds {
    width: calc(2 * ((100% - 2 * 15px) / 3) + 15px);
  }
  .one-fourth,
  .gallery-columns-4 .gallery-item {
    width: calc((100% - 3 * 15px) / 4);
  }
  .three-fourths {
    width: calc(3 * ((100% - 3 * 15px) / 4) + 2 * 15px);
  }
  .one-fifth,
  .gallery-columns-5 .gallery-item {
    width: calc((100% - 4 * 15px) / 5);
  }
  .two-fifths {
    width: calc(2 * ((100% - 4 * 15px) / 5) + 15px);
  }
  .three-fifths {
    width: calc(3 * ((100% - 4 * 15px) / 5) + 2 * 15px);
  }
  .four-fifths {
    width: calc(4 * ((100% - 4 * 15px) / 5) + 3 * 15px);
  }
  .one-sixth,
  .gallery-columns-6 .gallery-item {
    width: calc((100% - 5 * 15px) / 6);
  }
  .five-sixths {
    width: calc(5 * ((100% - 5 * 15px) / 6) + 4 * 15px);
  }
  .first {
    clear: both;
    margin-left: 0;
  }
}

