/* Like Box */
.like-box {
  float: left;
  font-size: 20.352px;
  font-size: 2.4rem;
  padding: 0;
  cursor: pointer;
  margin: 0;
  position: relative;
}

.like-box:hover {
  background-color: #9B9B9B;
}

.saved-artworks .like-box {
  position: absolute;
  top: 8.48px;
  top: 1rem;
  right: 42.4px;
  right: 5rem;
}

.saved-artworks .like-box .fa-heart-o, .saved-artworks .like-box .fa-close {
  background-color: rgba(0, 0, 0, 0.7);
}

.saved-artworks .like-box .fa-heart-o.hidden, .saved-artworks .like-box .fa-close.hidden {
  display: none;
}

.saved-artworks .like-box[data-exists="yes"] .fa-heart-o, .saved-artworks .like-box .fa-close {
  visibility: visible;
  opacity: 1;
  color: #ffffff;
  border: 0;
}

.like-count {
  padding-left: 8.48px;
  padding-left: 1rem;
  display: none;
}

.like-box .fa-heart-o, .like-box .fa-close {
  transition: all .35s ease-out;
  position: absolute;
  left: 0;
  top: 4.24px;
  top: .5rem;
  padding: 5.936px;
  padding: .7rem;
  border-radius: 100px;
  border: 1px solid #212121;
}

.like-box .fa-heart {
  left: 0;
  top: 4.24px;
  top: .5rem;
  transition: all .4s ease-out;
  position: absolute;
  visibility: hidden;
  transform: scale(0.2);
  opacity: 0;
  padding: 5.936px;
  padding: .7rem;
  border-radius: 100px;
  border: 1px solid #212121;
}

.like-box[data-exists="yes"] .fa-heart,
.like-box[data-exists="yes"] .like-tooltiptext.add {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
}

.like-box[data-exists="yes"] .fa-heart-o,
.like-box[data-exists="yes"] .like-tooltiptext.remove {
  visibility: hidden;
  opacity: 0;
}

.like-tooltip {
  position: relative;
}

.like-tooltip .like-tooltiptext {
  visibility: hidden;
  width: 110.24px;
  width: 13rem;
  background-color: rgba(33, 33, 33, 0.7);
  font-size: 10.176px;
  font-size: 1.2rem;
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  color: #ffffff;
  text-align: center;
  border-radius: 6px;
  padding: 4.24px 5.936px;
  padding: .5rem .7rem;
  /* Position the tooltip */
  position: absolute;
  top: 42.4px;
  top: 5rem;
  left: -38.16px;
  left: -4.5rem;
  z-index: 1;
}

.like-box:hover .like-tooltiptext {
  visibility: visible;
}

